/* styles.css */

/* Reset default link styles */
a {
  text-decoration: underline;
  display: inline-block;
  padding: 05px 10px; /* Adjust padding as needed */
  color: #0d05e9;
  background-color: #ebe014; /* Set the desired background color */
  padding-bottom: 2px; /* Adjust as needed to control the space between text and background */
  font-size: 18px;
  border: #e30e0e;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}
a:hover {
  background-color: #2980b9; /* Change color on hover */
}

/* Style for navigation links */
.nav-links {
  text-decoration: underline;
  font-size: 20px; /* Adjust the font size as needed */
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.nav-link {
  padding: 10px 20px;
  margin: 0 10px;
  font-size: 18px; /* Increase font size */
  border: 2px solid #3498db;
  border-radius: 5px;
  color: #3498db;
  transition: background-color 0.3s, color 0.3s;
}

.list a {
  padding: 10px 20px;
  text-decoration: none;
  font-size: 24px; /* Increase font size */
  color: var(--color-primary-400);
  background: linear-gradient(45deg, #3498db, #3498db); /* 3D background effect */
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  transition: color 0.3s;
}

.list a:hover,
.list a.active {
  color: var(--color-primary-800);
}
