/* homeStyles.css */


body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-color: #f0f0f0; /* Light background color */
}

.lte-technology {
    background-color: red;
    color: green;
}

.container {
    max-width: 400px;
    margin: 0 auto;
    padding: 200px;
}

.header {
    text-align: center;
    margin-bottom: 30px;
}

.header h1 {
    font-size: 36px;
    font-weight: bold;
    color: #d51616;
    text-align: center; /* Center the text */
}

.header h2 {
    font-size: 24px;
    color: #555;
}

.main-content {
    text-align: center;
    padding: 20px;
}

.main-content p {
    font-size: 18px;
    line-height: 1.5;
    color: #777;
}
